<template>
  <div class="input">
    <h1>{{ msg }}</h1>
    <label for="username">username</label>
    <input id="username" v-model="u" placeholder="username" />
    <label for="secret">secret</label>
    <input id="secret" v-model="s" placeholder="secret" />
    <button v-on:click="random">random</button>
    <label>timeinterval</label>
    30 <input type="radio" id="timeinterval30" value="30" v-model="timeinterval">
    60 <input type="radio" id="timeinterval60" value="60" v-model="timeinterval">
    <label for="time">time</label>
    <input id="time" v-model="t" placeholder="time" />
    <button v-on:click="now">now</button>
    <button v-on:click="backward">-30s</button>
    <button v-on:click="forward">+30s</button>
    <button v-on:click="generate">generate qr code</button>
    <div class="code-wrap">
      <p>onetime code:</p> 
      <p class="code">{{ token }}</p>
    </div>
  </div>
  <div class="csv">
    <pre>
      <code>
upn,serial number,secret key,timeinterval,manufacturer,model
{{ u }},1234567890,{{ s }},{{timeinterval}},testlab,hardwarekey</code>
    </pre>
  </div>
  <vue-qrcode v-if="qr" :value="qr" :options="option" tag="img"></vue-qrcode>

  <div>
    <p>{{qr}}</p>
  </div>
</template>

<script>
import VueQrcode from "@chenfengyuan/vue-qrcode";
import base32Encode from 'base32-encode'

const totp = require("totp-generator");

export default {
  components: {
    VueQrcode,
  },
  name: "HelloWorld",
  props: {
    msg: String,
    secret: String,
  },
  data() {
    return {
      u: "oath@yourtenant.onmicrosoft.com",
      s: this.secret,
      t: new Date().toISOString(),
      qr: null,
      timeinterval: 30
    };
  },
  computed: {
    token() {
      try {
        const d = Date.parse(this.t);
        return totp(this.s, { timestamp: d , period: this.timeinterval });
      } catch (e) {
        return "something wrong" + e;
      }
    },
  },
  methods: {
    now() {
      this.t = new Date().toISOString();
    },
    forward() {
      const d = new Date(this.t);
      d.setSeconds(d.getSeconds() + 30);
      this.t = d.toISOString();
    },
    backward() {
      const d = new Date(this.t);
      d.setSeconds(d.getSeconds() - 30);
      this.t = d.toISOString();
    },
    generate() {
      this.qr = `otpauth://totp/${this.u}?secret=${this.s}&algorithm=SHA1&digits=6&period=${this.timeinterval}`;
    },
    random() {
      const len = 256/8; //256bit
      const data = new Uint8Array(len);
      self.crypto.getRandomValues(data);
      this.s = base32Encode(data, 'RFC4648', { padding: false }).toLowerCase();
    }
  },
};
</script>

